import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  Tabel,
  getToday,
  formatDate,
  moment,
  setItem,
  ToastNotification,
  getItem,
  Swal,
} from "../../../components/helpers/library";
import { getData2, putData2 } from "../../../config/axios";
import HeadPesanan from "./HeadPesanan";

class LihatPesanan extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      datapesanan: [],
      columns: [
        {
          dataField: "no_pesanan",
          text: "No Pesanan",
          footerAttrs: {
            colSpan: "4",
          },
          footer: "Total",
        },
        {
          dataField: "tanggal_transaksi",
          text: "Tgl Transaksi",
        },
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },
        {
          dataField: "no_hp",
          text: "Nama Customer",
        },
        {
          dataField: "total_harga",
          text: "Total Harga",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("ID-id")}</span>;
          },
        },
        {
          dataField: "jumlah_bayar",
          text: "Jumlah Pembayaran",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("ID-id")}</span>;
          },
        },
        {
          dataField: "sisa_bayar",
          text: "Sisa Bayar",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("ID-id")}</span>;
          },
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    disabled={
                      row.tanggal_transaksi === getToday() ? false : true
                    }
                    data-tooltip={
                      row.tanggal_transaksi === getToday()
                        ? "Batal Pesanan"
                        : "Tidak Bisa Dibatal"
                    }
                    onClick={() => this.batalPesanan(row)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-window-close"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  batalPesanan(row) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Membatalkan Pesanan Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let tanggal = getItem("tgl_laporan");
        putData2("pesanan/cancel", { no_pesanan: row.no_pesanan })
          .then((res) => {
            ToastNotification("success", "Pesanan Berhasil Di Batal");
            getData2(
              "pesanan/" + tanggal.tgl_awal + "/" + tanggal.tgl_akhir
            ).then((res) => {
              // console.log(res.data);
              this.setState({
                datapesanan: res.data,
              });
            });
          })
          .catch((err) => {
            ToastNotification("info", err.response.data.message);
          });
      }
    });
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    getData2("pesanan/" + data.tgl_awal + "/" + data.tgl_akhir)
      .then((res) => {
        // console.log(res.data);
        if (res.data.length === 0) {
          ToastNotification("info", "Data Pesanan Tidak Ada");
        }
        // res
        let pesanan = [];
        res.data.forEach((element) => {
          let row = {
            no_pesanan: element.no_pesanan,
            nama_customer: element.nama_customer,
            no_hp: element.no_hp,
            sisa_bayar: element.sisa_bayar,
            jumlah_bayar: element.jumlah_bayar,
            total_harga: element.total_harga,
            tanggal_transaksi: element.tanggal,
            detail_barang: element.detail_barang,
          };
          pesanan.push(row);
        });

        let data_post = {
          tgl_awal: formatDate(
            moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
          ),
          tgl_akhir: formatDate(
            moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
          ),
        };
        this.setState({
          isLoading: false,
          datapesanan: pesanan,
        });
        setItem("tgl_laporan", data_post);
      })
      .catch((err) => {
        ToastNotification("info", err.response.data.message);
        this.setState({
          isLoading: false,
        });
      });
  }

  render() {
    const expandRow = {
      renderer: (row) => (
        <div className="container">
          <div className="row">
            <div className="col-12">
              Detail Barang
              <table>
                <tr>
                  <td> Kode Kategori </td>
                  <td> Perkiraan Berat </td>
                  <td> Perkiraan Kadar </td>
                  <td> Harga / Gram </td>
                  <td> Attribut </td>
                  <td> Harga Attribut </td>
                  <td> Kode Barcode </td>
                  <td> Ongkos </td>
                  <td> Total </td>
                  <td> keterangan </td>
                  <td> Deskripsi </td>
                  <td> Deskripsi Contoh </td>
                </tr>
                {row.detail_barang.map((detail, index) => (
                  <>
                    <tr>
                      <td> {detail.kode_kategori}</td>
                      <td> {detail.perkiraan_berat.toFixed(3)}</td>
                      <td> {detail.perkiraan_kadar}</td>
                      <td> {detail.harga_gram.toLocaleString("ID-id")}</td>
                      <td> {detail.attribut}</td>
                      <td> {detail.harga_atribut.toLocaleString("ID-id")}</td>
                      <td> {detail.kode_barcode}</td>
                      <td> {detail.ongkos.toLocaleString("ID-id")}</td>
                      <td> {detail.sub_total.toLocaleString("ID-id")}</td>
                      <td> {detail.keterangan}</td>
                      <td> {detail.deskripsi}</td>
                      <td> {detail.deskripsi_contoh}</td>
                    </tr>
                  </>
                ))}
              </table>
            </div>
          </div>
        </div>
      ),
      showExpandColumn: true,
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Pesanan</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Pesanan</li>
        </ol>
        <h1 className="page-header">Lihat Pesanan </h1>
        <Panel>
          <PanelHeader>Lihat Pesanan</PanelHeader>
          <br />
          {/* Lihat Pesanan */}
          <div className="container">
            <HeadPesanan
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
            />
            <Tabel
              keyField="no_pesanan"
              exportCsv={false}
              data={this.state.datapesanan}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Barang Kosong"
              expandRow={expandRow}
            />
          </div>
          <br />
          {/* End Tambah Lihat Pesanan  */}
        </Panel>
      </div>
    );
  }
}

LihatPesanan = reduxForm({
  form: "LihatPesanan",
  enableReinitialize: true,
})(LihatPesanan);
export default connect()(LihatPesanan);
