const ValidasiFormDataMemberPenjualan = (value) => {
    const errors = {};
    if (!value.kode_sales) {
        errors.kode_sales = "Kode Sales Harus Dipilih";
    }
    if (!value.jenis_pelanggan) {
        errors.jenis_pelanggan = "Jenis Pelanggan Harus Dipilih";
    }
    if (!value.nama_customer) {
        errors.nama_customer = "Nama Customer Tidak Boleh Kosong";
    }
    if (!value.no_hp) {
        errors.no_hp = "No HP Tidak Boleh Kosong";
    }
    if (!value.alamat_customer) {
        errors.alamat_customer = "Alamat Customer Tidak Boleh Kosong";
    }

    return errors;
}

export default ValidasiFormDataMemberPenjualan;


