import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { getData } from "../../../config/axios.jsx";
import HeadFormPoint from "./HeadFormPoint.jsx";

class FormTransaksiPoin extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      point: "POINT",
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.getKodeMember = this.getKodeMember.bind(this);
  }

  handleSubmit(data) {}
  getKodeMember(e) {
    getData("member/get/by-kode-member/" + e.kode_member).then((res) => {
      // console.log(res.data);
      if (res.data.length === 0) {
        ToastNotification('info', 'Kode Member Tersebut Tidak Ada')
        document.getElementById("transaksi_poin_nama_member").value =""
        document.getElementById("transaksi_poin_alamat").value =""
        document.getElementById("transaksi_poin_no_hp").value = ""
        this.setState({
          datatransaksimember: [],
          point: 0,
        });
      } else {
        localStorage.setItem("data_member", JSON.stringify(res.data));
        res.data.forEach((item, index) => {
          document.getElementById("transaksi_poin_nama_member").value =
            item.nama_customer;
          document.getElementById("transaksi_poin_alamat").value =
            item.alamat_customer;
          document.getElementById("transaksi_poin_no_hp").value = item.no_hp;
          // this.props.change("point", item.point);
          this.setState({
            datatransaksimember: [],
            point: item.point || 0,
          });
        });
      }
      // console.log(res.data);
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Member</Link>
          </li>
          <li className="breadcrumb-item active">Transaksi Poin</li>
        </ol>
        <h1 className="page-header">Transaksi Poin </h1>
        <Panel>
          <PanelHeader>Transaksi Poin</PanelHeader>
          <br />
          {/* Transaksi Poin */}
          <div className="container">
            <HeadFormPoint
              onChange={(e) => this.getKodeMember(e)}
              //   onSubmit={(data) => this.handleSubmit(data)}
              point={this.state.point}
            />
          </div>
          <br />
          {/* End Tambah Transaksi Poin  */}
        </Panel>
      </div>
    );
  }
}

export default connect(null)(FormTransaksiPoin);
