import React from "react";
import Avatar from "react-avatar";
import { Link } from "react-router-dom";
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import Swal from "sweetalert2";
import { postData } from "../../../config/axios";
import { getUserdata } from "../../helpers/function";
import { ToastNotification } from "../../helpers/notification";

const logout = () => {
  Swal.fire({
    title: "Konfirmasi Keluar !!",
    text: "Apkah anda ingin keluar ?",
    icon: "info",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      // window.location.reload();
      // this.props.history.push("/")
      window.location.href='/';
      localStorage.clear();
      localStorage.setItem("islogin", "false");
    }
  });
};
class DropdownProfile extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      dropdownOpen: false,
    };
  }

  toggle() {
    this.setState((prevState) => ({
      dropdownOpen: !prevState.dropdownOpen,
    }));
  }
  showModalTutupToko() {
		Swal.fire({
			html:
			  "<h1><b>Apakah Anda Yakin Ingin  <br/> " +
			  "Menutup Toko" +
			  "</b> ?</h1>",
			icon: "warning",
			position: "top-center",
			cancelButtonText: "Tidak",
			showCancelButton: true,
			confirmButtonText: "OK",
			showConfirmButton: true,
		  }).then((result) => {
			if (result.isConfirmed) {
				postData("system/close", { PROSES: "close" }).then((res) => {
					ToastNotification('success', 'Berhasil Menutup Toko')
					setTimeout(() => {
						window.location.reload();
						localStorage.clear();
						localStorage.setItem("islogin", "false");
					}, 1000);
				}).catch((err) => {
					ToastNotification('info',err.response.data)
				})
			}
		  });
	}
  render() {
    return (
      <Dropdown
        isOpen={this.state.dropdownOpen}
        toggle={this.toggle}
        className="dropdown navbar-user"
        tag="li"
      >
        <DropdownToggle tag="a">
          <Avatar name={getUserdata().user_id} size="30" color="#00743C" round="20px" />
          <span className="d-none d-md-inline" style={{ cursor: "pointer" }}>
          </span>{" "}
          <b className="caret"></b>
        </DropdownToggle>
        <DropdownMenu className="dropdown-menu dropdown-menu-right" tag="ul">
          {/* <DropdownItem>Edit Profile</DropdownItem> */}
          {/* <DropdownItem>Setting</DropdownItem> */}
          <DropdownItem onClick={()=>this.showModalTutupToko()}>Tutup Toko</DropdownItem>
          <div className="dropdown-divider"></div>
          <DropdownItem>
            {/* <button onClick={() => logout()}>Log Out </button> */}
            <Link to="#" onClick={() => logout()}>
              {" "}
              Logout{" "}
            </Link>
          </DropdownItem>
        </DropdownMenu>
      </Dropdown>
    );
  }
}

export default DropdownProfile;
