export const getNokirim = (data) => {
  return (dispatch) => {
    getData("kirimbarang/get/nokirim")
      .then((res) => {
        dispatch({
          type: GET_NO_KIRIM,
          payload: {
            data: res.data,
          },
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };
};
