  btnClear() {
    this.setState({
      isLoadingBtl: true,
    });
    setTimeout(() => {
      localStorage.removeItem("validasi_titipan");
      localStorage.removeItem("validasi_jual");
      localStorage.removeItem("validasi_beli");
      this.props.dispatch(reset("HeadValidasiJualBeliHutang"));
      this.props.dispatch(saveValidasiJual([]));
      this.props.dispatch(saveValidasiBeli([]));
      this.props.dispatch(disableButtonValidasi(false));
      this.setState({
        isLoadingBtl: false,
      });
    }, 1000);
  }
  componentWillUnmount() {
    this.props.dispatch(saveValidasiJual([]));
    this.props.dispatch(saveValidasiBeli([]));
  }
  handleSubmit(data) {
    let validasi_titipan = JSON.parse(localStorage.getItem("validasi_titipan"));
    let validasi_jual = JSON.parse(localStorage.getItem("validasi_jual"));
    let validasi_beli = JSON.parse(localStorage.getItem("validasi_beli"));
    let validasi_pesanan = JSON.parse(localStorage.getItem("validasi_pesanan"));
    let penjualan = [];
    let titipan = [];
    let pembelian = [];
    let pesanan = [];

    if (validasi_titipan.length === 0) {
      titipan = [];
    } else {
      validasi_titipan.forEach((item, index) => {
        let titipan_tmp = { no_faktur_group: item.no_faktur_group };
        titipan.push(titipan_tmp);
      });
    }
    if (validasi_jual.length === 0) {
      penjualan = [];
    } else {
      validasi_jual.forEach((item, index) => {
        let penjualan_tmp = { no_faktur_group: item.no_faktur_group };
        penjualan.push(penjualan_tmp);
      });
    }

    if (validasi_beli.length === 0) {
      pembelian = [];
    } else {
      validasi_beli.forEach((item, index) => {
        let pembelian_tmp = { no_faktur_group: item.no_faktur_group };
        pembelian.push(pembelian_tmp);
      });
    }
    if (validasi_pesanan.length === 0) {
      pesanan = [];
    } else {
      validasi_pesanan.forEach((item, index) => {
        let pesanan_tmp = { no_pesanan: item.no_pesanan };
        pesanan.push(pesanan_tmp);
      });
    }

    let hasil = {
      penjualan: penjualan,
      pembelian: pembelian,
    };
    let hasil2 = {
      titipan: validasi_titipan,
    }
    let hasil3 = {
      pesanan: validasi_pesanan,
    }

    postData2("pesanan/validate", hasil3).then((res) => {
      ToastNotification('success', res.data);
      localStorage.removeItem("validasi_pesanan");
      this.props.dispatch(reset("HeadValidasiJualBeliHutang"));
      this.props.dispatch(saveValidasiPesanan([]));
      this.props.dispatch(disableButtonValidasi(false));
    }).catch((err) => {
        ToastNotification("info", "Terjadi Kesalahaan Saat Mengirim Data");
    })

    postData2("titip/validate", hasil2).then((res) => {
      ToastNotification('success', res.data);
      localStorage.removeItem("validasi_titipan");
      this.props.dispatch(reset("HeadValidasiJualBeliHutang"));
      this.props.dispatch(saveValidasiTitipan([]));
      this.props.dispatch(disableButtonValidasi(false));
    }).catch((err) => {
        ToastNotification("info", "Terjadi Kesalahaan Saat Mengirim Data");
    })
    postData("validasi/simpan/jualbeli", hasil)
      .then((res) => {
        console.log(res);
        // return false;
        if (res.data.notajual.length !== 0) {
          CetakFakturJualBeli(res.data);
        }
// console.log(res.data.notabeli);
if (res.data.notabeli.length !== 0) {
let feedback = '';
res.data.notabeli.forEach((row, index) => {
  let jml_alamat = row.head.n_alamat_customer.length;
feedback += `========================================
Tanggal          : ${row.head.tgl_system}
Nama Customer    : ${row.head.nama_customer}
No Hp            : ${row.head.no_hp}
Alamat Customer  : ${row.head.n_alamat_customer.split(0,20)}
`
if(jml_alamat>20){
feedback += `                   ${row.head.n_alamat_customer.split(0,20)}
`;
}
if(jml_alamat>40){
feedback += `                   ${row.head.n_alamat_customer.split(40,20)}
`;
}
feedback += `========================================\n`
feedback += `Keterangan\n`
feedback += `========================================\n`
let total = 0;
let harga_nota = 0;
row.detail.forEach((detail, index) => {
total = total + detail.harga;
harga_nota = harga_nota + detail.harga_nota;
feedback += `No Faktur Beli : ${detail.no_faktur_beli}\n`
feedback += `Jenis Barang   : ${detail.kode_dept}\n`
feedback += `Nama Barang    : ${detail.nama_barang}\n`
feedback += `Berat          : ${detail.berat.toFixed(3)}\n`
feedback += `Harga          : ${detail.harga.toLocaleString("ID-id")}\n`
feedback += `========================================\n`
})
feedback += `Total          : ${total.toLocaleString('ID-id')}\n`
feedback += `========================================\n`
feedback += `Harga Waktu Beli   : ${total.toLocaleString('ID-id')}\n`
feedback += `Harga Waktu Jual   : ${harga_nota.toLocaleString('ID-id')}\n`
feedback += `



`
})
          document.getElementById("myInput").value = feedback;
        setTimeout(() => {
          this.TextFile();
        }, 100);
}
        localStorage.removeItem("validasi_jual");
        localStorage.removeItem("validasi_beli");
        this.props.dispatch(reset("HeadValidasiJualBeliHutang"));
        this.props.dispatch(saveValidasiJual([]));
        this.props.dispatch(saveValidasiBeli([]));
        this.props.dispatch(disableButtonValidasi(false));
        ToastNotification("success", "Validasi Transaksi Berhasil");
      })
      .catch((err) => {
        console.log(err.response.data);
        ToastNotification("info", "Terjadi Kesalahaan Saat Mengirim Data");
      });
  }
  TextFile() {
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("myInput").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_pembelian.txt";
    document.body.appendChild(element);
    element.click();
  }
