import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { getDataSales } from "../../../actions/datamaster_action";
import { getData, postData2 } from "../../../config/axios";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import {
  disableButtonValidasi,
  saveValidasiBeli,
  saveValidasiJual,
  saveValidasiPesanan,
  saveValidasiTitipan,
} from "../../../actions/hutang_action";
import { Panel, PanelHeader } from "../../../components/panel/panel";

const maptostate = (state) => {
  // let hasil = parseInt(localStorage.getItem('total_harga_jual')) - parseInt(localStorage.getItem('total_harga_beli'))
  // let datausersvalidasi = localStorage.getItem('datausersvalidasi')===null ? [] : JSON.parse(localStorage.getItem('datausersvalidasi'));
  return {
    DataSales: state.datamaster.getDataSales,
    saveValidasiBeli: state.hutang.saveValidasiBeli,
    saveValidasiJual: state.hutang.saveValidasiJual,
    saveValidasiTitipan: state.hutang.saveValidasiTitipan,
    saveValidasiPesanan: state.hutang.saveValidasiPesanan,
    btnValidasi: state.hutang.disableButtonValidasi,
  };
};
class HeadValidasiJualBeli extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      DataValidasi: [],
      btnValidasi: false,
      kode_sales: false,
      warna :false,
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataSales());
    // this.props.dispatch(disableButtonValidasi());
    if (this.props.saveValidasiJual.length === 0 && this.props.saveValidasiBeli.length === 0 && this.props.saveValidasiTitipan.length === 0) {
      localStorage.setItem(
        "validasi_jual",
        JSON.stringify([])
      );
      localStorage.setItem(
        "validasi_beli",
        JSON.stringify([])
      );
      localStorage.setItem(
        "validasi_titipan",
        JSON.stringify([])
      );
    }
  }
  getDataSales(e) {
    localStorage.setItem(
      "validasi_jual",
      JSON.stringify([])
    );
    localStorage.setItem(
      "validasi_beli",
      JSON.stringify([])
    );
    localStorage.setItem(
      "validasi_titipan",
      JSON.stringify([])
    );
    this.setState({
      kode_sales: e,
    });
    getData("sales/get/by-kode-sales/" + e)
      .then((res) => {
        // localStorage.setItem('datausersvalidasi',JSON.stringify(res.data))
        this.props.change("nama_sales", res.data.nama_sales);
        this.props.dispatch(saveValidasiJual([]));
        this.props.dispatch(saveValidasiBeli([]));
      })
      .catch((err) => {
        ToastNotification("info", "Gagal Mengambil Data Keserver");
      });
  }
  getDetaValidasi() {

    this.setState({
      isLoading: true,
    });
    let hasil = {
      kode_sales: this.state.kode_sales,
    };
    if (hasil.kode_sales === false) {
      this.setState({
        isLoading: false,
      });
      ToastNotification("info", "Kode Sales Harus Di pilih");
      return false;
    }
    postData2("validasi/get", hasil)
      .then((res) => {
        if (
          res.data[0].pembelian.length === 0 &&
          res.data[0].penjualan.length === 0 &&
          res.data[0].titipan.length === 0 &&
          res.data[0].pesanan.length === 0
        ) {
          ToastNotification("info", "Belum Ada Data Yang Harus Di Validasi");
          this.setState({
            isLoading: false,
          });
          this.props.change('grandtotal',0)
        } else {
          let harga_total = 0;
          res.data[0].penjualan.forEach(element => {
            harga_total = harga_total + element.harga_total
          });

          let harga = 0;
          res.data[0].pembelian.forEach(element => {
            harga = harga + element.harga
          });

          let harga_total_titipan = 0;
          res.data[0].titipan.forEach(element => {
            harga_total_titipan = harga_total_titipan + element.harga_total
          });

          let harga_total_pesanan = 0;
          res.data[0].titipan.forEach(element => {
            harga_total_pesanan = harga_total_pesanan + element.harga_total
          });

          // let hasil = Math.abs(parseInt(harga) - parseInt(harga_total));
          let hasil =parseInt(harga_total) - parseInt(harga) - parseInt(harga_total_titipan) - parseInt(harga_total_pesanan);
          // console.log(hasil);
          if (hasil < 0) {
            // console.log('bener');
            this.setState({"warna" : true})
          } else {
            this.setState({"warna" : false})
          }
          this.props.change('grandtotal',hasil.toLocaleString('ID-id'))
          ToastNotification("success", "Data Validasi Tersedia");
          this.setState({
            isLoading: false,
          });
          this.props.dispatch(disableButtonValidasi(true));
          localStorage.setItem(
            "validasi_jual",
            JSON.stringify(res.data[0].penjualan)
          );
          localStorage.setItem(
            "validasi_beli",
            JSON.stringify(res.data[0].pembelian)
          );
          localStorage.setItem(
            "validasi_titipan",
            JSON.stringify(res.data[0].titipan)
          );

          
          let pesanan = [];
          res.data[0].pesanan.forEach(element => {
              element.detail_barang.forEach(detail => {
                let row = {
                  no_pesanan: element.no_pesanan,
                  kode_kategori : detail.kode_kategori,
                  perkiraan_berat : detail.perkiraan_berat,
                  perkiraan_kadar : detail.perkiraan_kadar,
                  harga_gram : detail.harga_gram,
                  harga_atribut : detail.harga_atribut,
                  ongkos : detail.ongkos,
                  sub_total : detail.sub_total,
                  keterangan : detail.keterangan,
                  deskripsi : detail.deskripsi,
                  deskripsi_contoh : detail.deskripsi_contoh,
                }
                pesanan.push(row)
              });
          });

          localStorage.setItem(
            "validasi_pesanan",
            JSON.stringify(pesanan)
          );
          this.props.dispatch(saveValidasiJual(res.data[0].penjualan));
          this.props.dispatch(saveValidasiBeli(res.data[0].pembelian));
          this.props.dispatch(saveValidasiTitipan(res.data[0].titipan));
          this.props.dispatch(saveValidasiPesanan(pesanan));
        }
      })
      .catch((err) => {
        ToastNotification("info", "Terjadi Kesalahan Saat Request");
        this.setState({
          isLoading: false,
          DataValidasi: [],
        });
        this.props.dispatch(saveValidasiJual([]));
        this.props.dispatch(saveValidasiBeli([]));
        this.props.dispatch(saveValidasiTitipan([]));
        this.props.dispatch(saveValidasiPesanan([]));
        localStorage.setItem(
          "validasi_jual",
          JSON.stringify([])
        );
        localStorage.setItem(
          "validasi_beli",
          JSON.stringify([])
        );
        localStorage.setItem(
          "validasi_titipan",
          JSON.stringify([])
        );
        localStorage.setItem(
          "validasi_pesanan",
          JSON.stringify([])
        );
      });
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off"
      onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}
      >
        <div className="row">
          <div className="col-3">
            <Field
              name="kode_sales"
              onChange={(e) => this.getDataSales(e)}
              label="Pilih Kode Sales"
              placeholder="Masukan Kode Sales"
              options={this.props.DataSales.map((list) => {
                let data = {
                  value: list.kode_sales,
                  name: list.nama_sales,
                };
                return data;
              })}
              component={ReanderSelect}
            />
          </div>
          <div className="col-3">
            <Field
              name="nama_sales"
              component={ReanderField}
              type="text"
              label="Nama Sales"
              placeholder="Masukan Nama Sales"
              readOnly={true}
            />
          </div>
          <div className="col-3">
            <Field
              name="grandtotal"
              component={ReanderField}
              type="text"
              label="Grand Total"
              placeholder="Grand Total"
              readOnly={true}
              customeCss={this.state.warna ? "customeInput" : ""}
            />
          </div>
          <div className="col-3">
            <label>&nbsp;</label>
            <button
              onClick={() => this.getDetaValidasi()}
              className="btn btn-primary btn-block"
              disabled={this.state.isLoading}
              type="button"
            >
              {this.state.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Cari Data
                </>
              ) : (
                "Cari Data"
              )}
            </button>
          </div>
          {/* <div className="col-3">
          <Field
              name="grand_total"
              component={ReanderField}
              type="text"
              label="Grand Total"
              placeholder="Grand Total"
              readOnly={true}
            />
          </div> */}
          <div className="col-12">
            <Panel>
              <PanelHeader>List Data Penjualan</PanelHeader>
              <br />
              <Tabel
                keyField="kode_barcode"
                columns={this.props.columnsJual}
                data={this.props.saveValidasiJual || []}
                empty={true}
              />
            </Panel>
          </div>
          <div className="col-12">
            <Panel>
              <PanelHeader>List Data Pembelian</PanelHeader>
              <br />
              <Tabel
                keyField="kode_dept"
                columns={this.props.columnsBeli}
                data={this.props.saveValidasiBeli || []}
                empty={true}
              />
            </Panel>
          </div>
          <div className="col-12">
            <Panel>
              <PanelHeader>List Data Titipan</PanelHeader>
              <br />
              <Tabel
                keyField="kode_dept"
                columns={this.props.columnsTitipan}
                data={this.props.saveValidasiTitipan || []}
                empty={true}
              />
            </Panel>
          </div>
          <div className="col-12">
            <Panel>
              <PanelHeader>List Data Pesanan</PanelHeader>
              <br />
              <Tabel
                keyField="kode_dept"
                columns={this.props.columnsPesanan}
                data={this.props.saveValidasiPesanan || []}
                empty={true}
              />
            </Panel>
          </div>
          <div className="col-6">
            {this.props.btnValidasi === false ? (
              <button
                className="btn btn-primary btn-block"
                disabled={true}
                type="button"
              >
                Validasi
              </button>
            ) : (
              <button
                className="btn btn-primary btn-block"
                disabled={this.props.isLoading}
                type="submit"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Validasi
                  </>
                ) : (
                  "Validasi"
                )}
              </button>
            )}
          </div>
          <div className="col-6">
            <button
              className="btn btn-warning btn-block"
              disabled={this.props.isLoadingBtl}
              onClick={this.props.btnClear}
              type="button"
            >
              {this.props.isLoadingBtl ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Batal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

HeadValidasiJualBeli = reduxForm({
  form: "HeadValidasiJualBeliHutang",
  enableReinitialize: true,
})(HeadValidasiJualBeli);
export default connect(maptostate, null)(HeadValidasiJualBeli);
