import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm, reset } from "redux-form";
import { hideModal, showModal } from "../../../actions/datamaster_action.jsx";
import {
  showModalServiceAmbil,
  showModalServiceSelesai,
} from "../../../actions/services_action.jsx";
import { formatDate } from "../../../components/helpers/function.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import {
  ToastNotification,
  NotifSucces,
} from "../../../components/helpers/notification.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { postData } from "../../../config/axios.jsx";
import HeadLihatServices from "./HeadLihatServices.jsx";
import ModalPembayaran from "./ModalPembayaran.jsx";
import ModalServicesAmbil from "./ModalServicesAmbil.jsx";
import ModalServicesBatal from "./ModalServicesBatal.jsx";
import ModalServicesSelesai from "./ModalServicesSelesai.jsx";
import * as moment from "moment-timezone";

const maptostate = (state) => {
  return {
    hideModal: state.datamaster.modalDialog,
  };
};
class LihatServices extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingServicesSelesai: false,
      isLoading: false,
      dataservices: [],
      FormModal: "",
      pembayaran: false,
      columns: [
        // {
        //   dataField: "tgl_system",
        //   text: "Tanggal System",
        //   footerAttrs: {
        //     colSpan: "4",
        //   },
        //   footer: "Total",
        // },
        {
          dataField: "no_faktur_service",
          text: "No Faktur",
          footerAttrs: {
            colSpan: "4",
          },
          footer: "Total",
        },
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },
        {
          dataField: "alamat_customer",
          text: "Alamat Customer",
        },
        {
          dataField: "no_hp",
          text: "No Hp",
        },
        {
          dataField: "dp_rp",
          text: "Dp Rp",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("ID-id")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("id-ID"),
        },
        {
          dataField: "total",
          text: "Total Rp",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("ID-id")}</span>;
          },
          footer: (columnData) =>
            parseInt(
              columnData.reduce((acc, item) => acc + item, 0)
            ).toLocaleString("id-ID"),
        },
        {
          dataField: "selesai",
          text: "Selesai",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    data-tooltip="Service Selesai"
                    onClick={() => this.showModalServiceSelesai(row)}
                    className="btn btn-success "
                  >
                    <i className="fas fa-check"></i>
                  </button>
                  &nbsp;
                  <button
                    type="button"
                    data-tooltip="Service Ambil"
                    onClick={() => this.showModalServiceAmbil(row)}
                    className="btn btn-warning"
                  >
                    <i className="fa fa-file-export"></i>
                  </button>
                  <br/>
                  <br/>
                  <button
                    type="button"
                    data-tooltip="Service Batal"
                    onClick={() => this.showBatalServices(row)}
                    className="btn btn-danger"
                  >
                    <i className="fa fa-window-close"></i>
                  </button>
                  &nbsp;
                  <button
                    type="button"
                    data-tooltip="Reprint Service"
                    onClick={() => this.Reprint(row)}
                    className="btn btn-secondary"
                  >
                    <i className="fa fa-print"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: "",
        },
        // {
        //   dataField: "ambil",
        //   text: "Ambil",
        //   csvExport: false,
        //   headerClasses: "text-center",
        //   formatter: (rowcontent, row, index) => {
        //     return (
        //       <div className="row text-center">
        //         <div className="col-12">
        //           <button
        //             type="button"
        //             onClick={() => this.showModalServiceAmbil(row)}
        //             className="btn btn-warning mr-3"
        //           >
        //             <i className="fa fa-file-export"></i>
        //           </button>
        //         </div>
        //       </div>
        //     );
        //   },
        //   footer: "",
        // },
        // {
        //   dataField: "batal",
        //   text: "Batal",
        //   csvExport: false,
        //   headerClasses: "text-center",
        //   formatter: (rowcontent, row, index) => {
        //     return (
        //       <div className="row text-center">
        //         <div className="col-12">
        //           <button
        //             type="button"
        //             onClick={() => this.showBatalServices(row)}
        //             className="btn btn-danger mr-3"
        //           >
        //             <i className="fa fa-window-close"></i>
        //           </button>
        //         </div>
        //       </div>
        //     );
        //   },
        //   footer: "",
        // },
        // {
        //   dataField: "print",
        //   text: "Print",
        //   csvExport: false,
        //   headerClasses: "text-center",
        //   formatter: (rowcontent, row, index) => {
        //     return (
        //       <div className="row text-center">
        //         <div className="col-12">
        //           <button
        //             type="button"
        //             onClick={() => this.Reprint(row)}
        //             className="btn btn-secondary mr-3"
        //           >
        //             <i className="fa fa-print"></i>
        //           </button>
        //         </div>
        //       </div>
        //     );
        //   },
        //   footer: "",
        // },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    localStorage.setItem("total_rp_services", JSON.stringify([]));
  }
  showBatalServices(data) {
    localStorage.setItem("total_rp_service_ambil", JSON.stringify([]));
    this.setState({ FormModal: "SERVICE_BATAL", pembayaran: true });
    // this.props.dispatch(showModal());
    this.props.dispatch(showModalServiceAmbil(data));
  }
  Reprint(id) {
    let hasil = {
      no_faktur_service :  id.no_faktur_service
    }
    
postData('service/get/bynota', hasil).then((res) => {
  console.log(res.data.notaservice);
let feedback = '';
res.data.notaservice.forEach((row, index) => {
let jml_alamat = row.n_alamat_customer.length;
feedback = `========================================
Tanggal          : ${row.tgl_system}
Kode Sales       : ${row.kode_sales}
Nama Customer    : ${row.nama_customer}
No Hp            : ${row.no_hp}
Alamat Customer  : ${row.n_alamat_customer.split(0,20)}
Total Service    : ${row.total_service.toLocaleString("ID-id")}
`
if(jml_alamat>20){
feedback += `                   ${row.n_alamat_customer.split(0,20)}
`;
}
if(jml_alamat>40){
feedback += `                   ${row.n_alamat_customer.split(40,20)}
`;
}
feedback += `========================================\n`
feedback += `Keterangan\n`
feedback += `========================================\n`
let ongkos = 0;
let harga_nota = 0;
row.detail_barang.forEach((detail, index) => {
  ongkos = ongkos + detail.ongkos;
let jml_des = detail.deskripsi.length;
harga_nota = harga_nota + detail.harga_nota;
feedback += `Nama Barang      : ${detail.nama_barang}\n`
feedback += `Berat            : ${detail.berat.toFixed(3)}\n`
feedback += `Jumlah           : ${detail.jumlah.toLocaleString("ID-id")}\n`
feedback += `Ongkos           : ${detail.ongkos.toLocaleString("ID-id")}\n`
feedback += `deskripsi        : ${detail.deskripsi.split(0,20)}\n`
if(jml_des>20){
feedback += `                   ${row.deskripsi.split(0,20)}
  `;
}
if(jml_des>40){
feedback += `                   ${row.deskripsi.split(40,20)}
  `;
}
feedback += `========================================\n`
})
feedback += `Total Ongkos     : ${ongkos.toLocaleString('ID-id')}\n`
feedback += `========================================\n`
feedback += `






`
})

      document.getElementById("myInput").value = feedback;

    }).then(() => {
      console.log('masuk');
      const element = document.createElement("a");
      const file = new Blob([document.getElementById("myInput").value], {
        type: "text/plain;charset=utf-8",
      });
      element.href = URL.createObjectURL(file);
      element.download = "autoprint_service.txt";
      document.body.appendChild(element);
      element.click();
    })
      
      .catch((err) => {
      
    })
  }
  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    this.setState({
      isLoading: true,
    });
    postData("service/get/by-tanggal", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Services Tidak Ada");
          this.setState({
            isLoading: false,
            dataservices: [],
          });
        } else {
          ToastNotification("success", "Data Services Tersedia");
          this.setState({
            isLoading: false,
            dataservices: res.data,
          });
        }
      })
      .catch((err) => this.errorhandling(err));
  }
  errorhandling(error) {
    this.setState({
      isLoading: false,
      dataservices: [],
    });
    ToastNotification("info", "Transaksi Gagal Disimpan");
  }
  showModalServiceSelesai(data) {
    this.props.dispatch(showModal());
    this.props.dispatch(showModalServiceSelesai(data));
    this.setState({
      // FormModal: true,
      pembayaran: false,
    });
  }
  showModalPembayaran() {
    let tgl_ambil = document.getElementById("tgl_ambil").value;

    if (tgl_ambil === "") {
      ToastNotification("info", "Tanggal Ambil Harus Diisi");
      return false;
    } else {
      this.props.dispatch(showModal());
    }
  }
  showModalServiceAmbil(data) {
    localStorage.setItem("total_rp_service_ambil", JSON.stringify([]));
    this.setState({ FormModal: "SERVICE_AMBIL", pembayaran: true });
    // this.props.dispatch(showModal());
    this.props.dispatch(showModalServiceAmbil(data));
  }
  simpanServicesSelesai(data) {
    this.setState({
      isLoadingServicesSelesai: true,
    });
    let hasil = {
      no_faktur_service: data.no_faktur_services,
      tgl_selesai: data.tgl_selesai,
    };
    postData("service/selesai", hasil)
      .then((res) => {
        NotifSucces(res.data);
        this.setState({
          isLoadingServicesSelesai: false,
        });
        this.props.dispatch(reset("ModalServicesSelesai"));
      })
      .then(() => {
        this.props.dispatch(hideModal());
        localStorage.setItem("total_rp_services", JSON.stringify([]));
      })
      .catch((err) => {
        this.setState({
          isLoadingServicesSelesai: false,
        });
        ToastNotification("info", "Data Services Gagal Disimpan");
      });
    // console.log(hasil);/
  }

  simpanServicesAmbil(data) {
    let no_faktur = document.getElementById("no_faktur_services_ambil").value;
    let tgl_ambil = document.getElementById("tgl_ambil").value;
    let total = parseInt(document.getElementById("total_bayar").value);
    let data_pembayaran = {
      jenis: data.jenis,
      jumlah_rp: data.cash,
    };
    let hasil = "";
    if (data.grand_total_pembayaran === undefined) {
      hasil = {
        no_faktur_service: no_faktur,
        tgl_ambil: tgl_ambil,
        total: total,
        pembayaran: [
          {
            jenis: "CASH",
            jumlah_rp: 0,
          },
        ],
      };
    } else {
      if (data.cash < data.grand_total_pembayaran) {
        let cash_bayar =
          parseInt(data.grand_total_pembayaran) - parseInt(data.cash);
        ToastNotification(
          "info",
          "Pembayaran Masih Kurang Rp." + parseInt(cash_bayar).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
        );
        return false;
      } else {
        hasil = {
          no_faktur_service: no_faktur,
          tgl_ambil: tgl_ambil,
          total: total,
          pembayaran: [data_pembayaran],
        };
      }
    }
    // console.log(hasil);
    postData("service/ambil", hasil)
      .then((res) => {
        NotifSucces(res.data);
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        this.props.dispatch(hideModal());
        this.setState({
          FormModal: "",
          pembayaran: false,
          dataservices: [],
        });
      })
      .catch((err) => {
        ToastNotification("info", "Data Services Gagal Disimpan");
      });
    // let hasil = {
    //   no_faktur_service: data.no_faktur_services,
    //   tgl_selesai: data.tgl_selesai,
    // };
    // console.log(hasil);
  }
  simpanServicesBatal(data) {
    // console.log(data);
    let hasil = {
      no_faktur_service: data.no_faktur_services,
    };
    postData("service/batal", hasil)
      .then((res) => {
        NotifSucces(res.data);
      })
      .then(() => {
        this.props.dispatch(reset("ModalPembayaran"));
      })
      .then(() => {
        this.props.dispatch(hideModal());
        this.setState({
          FormModal: "",
          pembayaran: false,
          dataservices: [],
        });
      })
      .catch((err) => {
        ToastNotification("info", "Data Services Gagal Disimpan");
      });
  }
  back() {
    localStorage.setItem("total_rp_service_ambil", JSON.stringify([]));
    this.setState({ FormModal: "" });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Services</Link>
          </li>
          <li className="breadcrumb-item active">Lihat Services</li>
        </ol>
        <h1 className="page-header">Lihat Services </h1>
        <Panel>
        <textarea
            style={{ display: "none" }}
            id="myInput"
            rows="100"
            cols="100"
          />

          <PanelHeader>Lihat Services</PanelHeader>
          <br />
          {/* Lihat Services */}

          <div className="container">
            {this.state.FormModal === "SERVICE_AMBIL" ? (
              <ModalServicesAmbil
                showModalPembayaran={() => this.showModalPembayaran()}
                back={() => this.back()}
                onSubmit={(data) => this.simpanServicesAmbil(data)}
              />
            ) : this.state.FormModal === "SERVICE_BATAL" ? (
              <ModalServicesBatal
                back={() => this.back()}
                onSubmit={(data) => this.simpanServicesBatal(data)}
              />
            ) : (
              <>
                <HeadLihatServices
                  onSubmit={(data) => this.handleSubmit(data)}
                  isLoading={this.state.isLoading}
                />
                <Tabel
                  keyField="no_faktur_group"
                  exportCsv={false}
                  data={this.state.dataservices}
                  columns={this.state.columns}
                  empty={true}
                  textEmpty="Data Barang Kosong"
                />
              </>
            )}
          </div>
        </Panel>
        <ModalGlobal
          title={<TitleModal state={this.state.pembayaran} />}
          content={
            this.state.pembayaran === true ? (
              <ModalPembayaran
                onSubmit={(data) => this.simpanServicesAmbil(data)}
              />
            ) : (
              <ModalServicesSelesai
                isLoadingServicesSelesai={this.state.isLoadingServicesSelesai}
                onSubmit={(data) => this.simpanServicesSelesai(data)}
              />
            )
          }
        />
      </div>
    );
  }
}

function TitleModal(data, onSubmit) {
  // console.log(state.state);
  if (data.state === true) {
    return "Form Pembayaran Service Ambil";
  } else {
    return "Form Services Selesai";
  }
}
// export default connect * LihatServices;

LihatServices = reduxForm({
  form: "LihatServices",
  enableReinitialize: true,
  // validate: ValidasiDataUsers,
})(LihatServices);
export default connect(maptostate, null)(LihatServices);
